.button {
  width: 150px;
  height: 50px;
  border: none;
  border-radius: 10px;
  background-color: var(--background-button);
  color: white;
  font-family: 'Inter', sans-serif;
  margin: 0 auto;
  cursor: pointer;
}

.button:active {
  box-shadow: inset 5px 5px 10px rgba(0, 0, 0, 0.5);
}
